// @flow
import React from 'react';
import type { AbstractComponent } from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiDialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';

const styles = theme => ({
  root: {
    padding: 0,
    textAlign: 'right',
  },
  closeButton: {
    borderRadius: 0,
    margin: '8px 8px 8px 0',
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
});

export const Dialog: AbstractComponent<typeof MuiDialog> = withStyles(
  theme => ({
    paper: {
      borderRadius: 0,
    },
  }),
)(props => {
  const { children, ...rest } = props;

  return <MuiDialog {...rest}>{children}</MuiDialog>;
});

export const DialogTitle: AbstractComponent<typeof MuiDialogTitle> = withStyles(
  styles,
)(props => {
  const { classes, onClose, ...rest } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...rest}>
      <IconButton
        aria-label="close"
        className={classes.closeButton}
        onClick={onClose}
      >
        <i className="fa fa-times"></i>
      </IconButton>
    </MuiDialogTitle>
  );
});

export const DialogContent: AbstractComponent<
  typeof MuiDialogContent,
> = withStyles(theme => ({
  root: {
    padding: '16px',
  },
}))(MuiDialogContent);

export const DialogActions: AbstractComponent<
  typeof MuiDialogActions,
> = withStyles(theme => ({
  root: {
    padding: 0,
    justifyContent: 'flex-start',
    width: '50%',
    marginTop: '-46px',
  },
}))(MuiDialogActions);
