// @flow
import React from 'react';
import type { Element, AbstractComponent } from 'react';
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from 'components/Dialog';
import type { Offer } from 'types/Offer';
import { FORM_TYPE_IDS, OFFER_STEPS } from 'shared/Consts';
import _get from 'lodash/get';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import Content from '../Step/Content';
import './Style.css';

type Props = {
  open: boolean,
  offer: Offer | typeof undefined,
  handleClickCloseDialog: Function,
  handleApproval: Function,
  handleDecline: Function,
  isSubmitting: boolean,
};

const checkProps = ({ offer }) => offer && _get(offer, ['OfferId']);

const CounterOfferDialog = (props: Props): Element<typeof Dialog> => {
  const {
    open,
    offer,
    isSubmitting,
    handleClickCloseDialog,
    handleApproval,
    handleDecline,
  } = props;

  const offerId = _get(offer, ['OfferId']);
  const stepId = _get(offer, ['StepId']);
  const formCounter = _get(offer, ['CounterOffer', 'Forms']).find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.counterOfferSubmission,
  );
  const formId = _get(formCounter, ['FormId']);
  const counterOfferId = _get(offer, ['CounterOffer', 'CounterOfferId']);

  const onClickApprovalHandler = () => {
    if (isSubmitting) return;

    handleApproval(offerId);
    handleClickCloseDialog();
  };

  const onClickDeclineHandler = () => {
    if (isSubmitting) return;

    handleDecline(offerId);
    handleClickCloseDialog();
  };

  return (
    <Dialog
      open={open}
      aria-labelledby="customized-dialog-title"
      onClose={handleClickCloseDialog}
      scroll="paper"
      fullWidth
      maxWidth="md"
    >
      <DialogTitle
        id="customized-dialog-title"
        disableTypography
        onClose={handleClickCloseDialog}
      />
      <DialogContent>
        <div
          className={`custom-dialog-content ${
            stepId === OFFER_STEPS.buyerAcceptCounterOffer
              ? 'content-without-submit'
              : ''
          }`}
        >
          <Content
            offerId={counterOfferId}
            step={OFFER_STEPS.counteredBySeller}
            formId={formId}
          />
          <DialogActions>
            <button
              className="next_btn animated_fastest"
              onClick={onClickApprovalHandler}
            >
              Accept
            </button>
            <button
              className="prev_btn animated_fastest"
              onClick={onClickDeclineHandler}
            >
              Decline
            </button>
          </DialogActions>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default (hideIfInvalidProps(checkProps)(
  CounterOfferDialog,
): AbstractComponent<Props>);
