import React from 'react';
import { useIsMobile } from 'hooks';

/**
 * HOC for responsive elements.
 *
 * @param {Object} props - properties.
 * @param {boolean} props.forMobile - whether for mobile or desktop.
 * @param {boolean} props.children - children components.
 */
const ResponsiveHOC = ({ forMobile = false, children }) => {
  const isMobile = useIsMobile();
  return forMobile === isMobile ? <>{children}</> : <></>;
};

export { ResponsiveHOC };
